export const dateFormatter = date => {
  const dateArray = date.split(`-`);
  const year = dateArray[0];
  const mounth = getMounthFromNumber(dateArray[1]);
  const day = dateArray[2];
  return `${day} ${mounth} ${year}`;
};

const getMounthFromNumber = number => {
  switch (number) {
    case `01`:
      return `January`;
    case `02`:
      return `February`;
    case `03`:
      return `March`;
    case `04`:
      return `April`;
    case `05`:
      return `May`;
    case `06`:
      return `June`;
    case `07`:
      return `July`;
    case `08`:
      return `August`;
    case `09`:
      return `September`;
    case `10`:
      return `October`;
    case `11`:
      return `November`;
    case `12`:
      return `December`;
  }
};
